.about {
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.profile-image-container,
.profile-image {
    opacity: 0;
}

.profile-image-container {
    padding: 20px;
    background: var(--primary);
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
    margin-bottom: 40px;
}

.profile-image {
    width: 6rem;
}


/* Text-animation */

.intro-heading, .intro-text {
    height: 45px;
    text-align: center;
}

@media (max-width:768px) {
    .intro-heading h1, .intro-text h1 {
        font-size: 35px;
    }
}

.heading,
.text {
    opacity: 0;
}

/* Game */

.letters-box, .buttons-box {
    transform: translateY(200px);
}

.letter-buttons {
    width: 40px;
    height: 40px;
    background: var(--primary);
    border: none;
    color: white;
    font: inherit;
    cursor: pointer;
    margin: 3px;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 25px;
}

.letter {
    margin: 3px;
    border-bottom: 4px solid var(--primary);
    text-transform: uppercase;
    font-weight: 700;
    font-size: 40px;
    width: 40px;
    height: 50px;
}

.no-border-letter {
    border: none !important;
}

@media (max-width: 768px) {
    .letter {
        font-size: 20px;
        margin: 3px;
        width: 20px;
        height: 30px;
    }
    .letter-buttons {
        width: 35px;
        height: 35px;
        font-size: 16px;
    }
}

.wrong {
    background: var(--red);
}

.right {
    background: var(--green);
}

.success-message {
    padding: 10px;
    transform: translateY(-200px);
}

.success-message-svg {
    opacity: 0;
    transform: scale(0);
}